import axios from 'axios';
import { onMounted, reactive, toRefs } from 'vue';
export default {
  name: 'bigSort',
  setup() {
    let data = reactive({
      big_sort: []
    });
    const getHomeBigSortData = () => {
      // axios，get请求
      axios.get('/home/getHomeData').then(res => {
        // console.log(res);
        const {
          code,
          homeData
        } = res.data;
        if (code === 200) {
          // 其他的small_sort也可以这样写
          data.big_sort = homeData.big_sort;
        }
      });
    };
    onMounted(() => {
      getHomeBigSortData();
    });
    return {
      ...toRefs(data)
    };
  }
};